  display: grid;
  grid-template-columns: repeat(10, 1fr);
  width: 300px;
  height: 300px;
}

.cell {
  border: 1px solid #ccc;
  cursor: pointer;
}

.ship {
  background-color: #999;
}

.hit {
  background-color: #f00;
}

.miss {
  background-color: #00f;
}

#message {
  display: none;
  margin-top: 20px;
  font-size: 24px;
  font-weight: bold;
}

#game-container {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.gameboard {
  margin: 0 10px;
}
